import React from 'react';
import 'rc-input-number/assets/index.css';
import 'rc-slider/assets/index.css';
import store from '../../../store';
import { changeAction } from '../../../actions/h5Actions';
import './actionPanel.less';

export default class AnimationPanel extends React.Component {
    state = {
        value: '无',
    };
    getName = name => {
        switch (name) {
        case 'WordModal': return '文字';
        case 'ShapeModal': return '图形';
        case 'ImageModal': return '图片';
        case 'AudioModal': return '音频';
        case 'VideoModal': return '视频';
        case 'NoteModal': return '备注';
        default: return '';
        }
    };

    onClick = e => {
        store.dispatch(changeAction(this.props.focus.id, Object.assign({}, this.props.focus.action, { target: e.target.value })));
    };

    onChange = e => {
        this.setState({
            value: e.target.value,
        });
        store.dispatch(changeAction(this.props.focus.id, Object.assign({}, this.props.focus.action, { name: e.target.value })));
    };
    changeValue = e => {
        store.dispatch(changeAction(this.props.focus.id, Object.assign({}, this.props.focus.action, { value: e.target.value })));
    };
    render() {
        return (
            <div className="actionPanel">
                <div className="title">
                    单击当前组件时
                    <select onChange={this.onChange}>
                        <option value="无">无</option>
                        <option value="显示">显示</option>
                        <option value="隐藏">隐藏</option>
                        <option value="跳转">跳转</option>
                    </select>
                </div>
                <div className={this.state.value !== '无' ? 'elements' : ''}>
                    {
                    this.state.value !== '无' && this.state.value !== '跳转' && this.props.page.elements.map((element, index) => (
                        <div key={element.id} className="item"><input type="radio" value={element.id} name="element" onClick={this.onClick} /> {this.getName(element.name)}{index}</div>
                    ))
                }
                    {
                    this.state.value === '跳转' && <div><input type="text" onChange={this.changeValue} placeholder="请输入链接地址" /></div>
                }
                </div>
            </div>
        );
    }
}



