import React from 'react';
import SkyLight from 'react-skylight';
import VideoModal from '../modal/VideoModal';
import Noty from '../../common/noty/noty';
import store from '../../../store';
import { addElements } from '../../../actions/h5Actions';
import './addVideoDialog.less';

export default class AddVideoDialog extends React.Component {
    show() {
        this.videoModal.show();
    }
    addVideo = () => {
        if (!this.input.value || this.input.value.trim().indexOf('<iframe') !== 0) {
            Noty.error('视频地址不正确');
            return;
        }
        store.dispatch(addElements(new VideoModal(this.input.value.trim()).plainObject()));
        this.videoModal.hide();
    };
    cancelClick = () => {
        this.videoModal.hide();
    };

    render() {
        return (
            <SkyLight hideOnOverlayClicked ref={com => { this.videoModal = com; }} title="添加视频">
                <div>
                    <p className="pTop">把视频通用代码复制到组建即可使用</p>
                    <input spellCheck="false" className="codeInpt" type="text" placeholder="通用代码" ref={input => this.input = input} />
                    <p className="Remarks">支持的视频：
                        <a href="javascript:void(0)">腾讯视频</a>、<a href="javascript:void(0)">Youtube</a>
                    </p>
                    <p className="positionP">
                        <button className="cancelVideo" onClick={this.cancelClick}>取消</button>
                        <button className="determineVideo" onClick={this.addVideo}>确定</button>
                    </p>
                </div>
            </SkyLight>
        );
    }
}



